
import React from 'react';
import './Header.css';
import './HeaderMedia.css';
import HeaderLeftMenuItem from './HeaderLeftMenuItem/HeaderLeftMenuItem';
import HeaderRowBootomItems from './HeaderRowBottomItems/HeaderRowBootomItems';
import HeaderLeftSubMenuItem from './HeaderLeftSubMenuItem/HeaderLeftSubMenuItem';

import { AppContext } from '../../contexts/AppContext';

// images
import logoImg from '../../images/logo/logo.svg';
import langRuImg from '../../images/logo/header-ru.png';
import langEnImg from '../../images/logo/header-en.png';

function Header(props) {

  // contexts
  const headerLeftMenuItems = React.useContext(AppContext).headerLeftMenu;
  const headerRowTopMenuItems = React.useContext(AppContext).headerRowTop;
  const headerRowBottomMenuItems = React.useContext(AppContext).headerRowBottom;
  const headerBunnerMenuItems = React.useContext(AppContext).headerBunner;

  // left sub menu=======================================================================
  const [isLeftSubMenuOpen, setIsLeftSubMenuOpen] = React.useState(false);
  const [clickedElementsKeyLeftMenu, setClickedElementsKeyLeftMenu] = React.useState('');

  function handleLeftMenuClick(itemKey) {
    function openLeftMenu() {
      setClickedElementsKeyLeftMenu(itemKey);
      setIsLeftSubMenuOpen(true);
    }

    if (isLeftSubMenuOpen === true) {      
      if (clickedElementsKeyLeftMenu === itemKey) {
        setIsLeftSubMenuOpen(false);
      } else {
        setIsLeftSubMenuOpen(false);
        setTimeout(() => {
          openLeftMenu();
        }, 500)
      }
    } else {
      openLeftMenu();
    }
  }

  React.useEffect(() => { 
    function handleLeftMenuOverlayClose(evt) {      
      if (!evt.target.closest('.header__left-item')
        && isLeftSubMenuOpen
        && !evt.target.closest('.header__left-submenu-item')) {
       setIsLeftSubMenuOpen(false);
      }
    }
    document.addEventListener('click', handleLeftMenuOverlayClose);
    return () => {
      document.removeEventListener('click', handleLeftMenuOverlayClose)
    }
  })


  //  city ==============================================================================
  const [isCitiesOpen, setIsCitiesOpen] = React.useState(false);

  function handleCityClick(e) {
    props.onChangeCity(e.target.id);
    setIsCitiesOpen(false);
  }

  function handleCitiesClick() {
    setIsCitiesOpen(!isCitiesOpen);
  }

  React.useEffect(() => { 
    function handleCitiesOverlayClose(evt) {      
      if (!evt.target.closest('.header__top-city-items')
        && isCitiesOpen){
        setIsCitiesOpen(false);
      }
    }
    document.addEventListener('click', handleCitiesOverlayClose);
    return () => {
      document.removeEventListener('click', handleCitiesOverlayClose)
    }
  })
  
  // lang ================================================================================
  let langImg = '';
  let langSubImg = '';
  if (props.lang === 'ru') {
    langImg = langRuImg;
    langSubImg = langEnImg;
  } else {
    langImg = langEnImg;
    langSubImg = langRuImg;
  }

  const [isLangOpen, setIsLangOpen] = React.useState(false);

  function handleLangClick() {
    setIsLangOpen(!isLangOpen)
  }

  function handleChangeLangClick () {
    props.onChangeLang();
    setIsLangOpen(false);
  }

  React.useEffect(() => { 
    function handleLangOverlayClose(evt) {      
      if (!evt.target.closest('.header__top-lang')
        && isLangOpen){
        // && !evt.target.closest('.header__top-banner')) {
        setIsLangOpen(false);
      }
    }
    document.addEventListener('click', handleLangOverlayClose);
    return () => {
      document.removeEventListener('click', handleLangOverlayClose)
    }
  })


  
  // bunner=================================================================================
  const [isBunnerOpen, setIsBunnerOpen] = React.useState(false);

  function handleBunnerClick() {
    setIsBunnerOpen(true);
  }
  function closeBunner () {
    setIsBunnerOpen(false);
  }

  React.useEffect(() => { 
    function handleBunnerOverlayClose(evt) {      
      if (!evt.target.closest('.header__top-banner')
        && isBunnerOpen) {
        closeBunner();
      }
    }
    document.addEventListener('click', handleBunnerOverlayClose);
    return () => {
      document.removeEventListener('click', handleBunnerOverlayClose)
    }
  })

  // burger =========================================================================
  const [isBurgerOpen, setIsBurgerOpen] = React.useState(false);

  function handleBurgerClick() {
    setIsBurgerOpen(!isBurgerOpen)
  }

  // sub-menu-top=====================================================================
  const [isSubMenuTopOpen, setISubMenuTopOpen] = React.useState(false);
  window.onscroll = function () {
  let scrollYPos = window.pageYOffset;
    if (scrollYPos > 0) {
      setISubMenuTopOpen(true);
    } else {
      setISubMenuTopOpen(false);
    }
  }

// =================================================================================

  return (    
    <>
      <header className="header">
        {/* <!-- left============================================== --> */}
        <div className="header__left">
          <div className="header__left-container">
            <div className={isLeftSubMenuOpen ? "header__left-cross header__left-cross_show" : "header__left-cross" }>
              <div className="cross cross_size_l header__left-cross-button"/>
            </div>
            <ul className="header__left-column">
              {Object.keys(headerLeftMenuItems).map((item, index) => {                   
                return ( 
                  <HeaderLeftMenuItem
                    key={index}
                    img={headerLeftMenuItems[item].img}
                    title={headerLeftMenuItems[item].title}
                    onClick={handleLeftMenuClick}
                    id={item}
                  />
                )
              })}
              <li className="header__left-item header__left-item-pdf" id="header__left-submenu-pdf-items">
                <div
                  className="header__left-item-img"
                  style={{ background: `url(${React.useContext(AppContext).headerLeftMenuPdf.img}) center/contain no-repeat` }}
                />
                <div className="header__left-text header__left-text-pdf">{React.useContext(AppContext).headerLeftMenuPdf.title}</div>
              </li>
            </ul>
          </div>
        </div>

        {/* <!-- submenu left --> */}
        <ul className={
          isLeftSubMenuOpen
            ? 'header__left-submenu-items header__left-submenu-items_opened'
            : 'header__left-submenu-items'
         }>
          {clickedElementsKeyLeftMenu && (
            Object.keys(headerLeftMenuItems[clickedElementsKeyLeftMenu].subItems).map((item, index) => { 
              return (
                <HeaderLeftSubMenuItem
                  key={index}
                  id={item}
                  title={headerLeftMenuItems[clickedElementsKeyLeftMenu].subItems[item]}
                />
              )
            }))
          }        
        </ul>

        {/* <!-- top ================================================== --> */}
        <div className="header__top">
          <div className={isSubMenuTopOpen ? "header__sub-menu-top header__sub-menu-top-active" : "header__sub-menu-top"}>
            <a href="#">
              <img src={logoImg} alt="Альта-Профиль" className="logo logo_place_sub-menu-top" />
            </a>
            <div className="header__top-basket">
              <p className="header__top-basket-quantity">
                {props.productQuantity < 10
                  ? '0' + props.productQuantity
                  : props.productQuantity}
              </p>
            </div>
          </div>
          
          <div className="header__top-container">

            {/* <!-- column 1 ==========--> */}
            <div className="header__top-column">
              <a href="#">
                <img src={logoImg} alt="Альта-Профиль" className="logo logo_place_header" />
              </a>

            </div>

            {/* <!-- column 2 =========--> */}
            <div
              className={
                isBurgerOpen ? "header__top-column_place_center-btn header__top-buerger-active" : "header__top-column_place_center-btn"
              }
              onClick={handleBurgerClick}
            >
              <span
                className={
                  isBurgerOpen ? "header__top-column_place_center-btn-span header__top-buerger-active" : "header__top-column_place_center-btn-span"
                }
              />
            </div>

            <div className={
              isBurgerOpen ? "header__top-column header__top-column_place_center header__top-buerger-active" : "header__top-column header__top-column_place_center"
              }
            >

              {/* <!-- row 1  --> */}
              <div className="header__top-row1">
                <div className="header__top-button">{headerRowTopMenuItems.button}</div>
                
                <div className="header__top-basket">
                  <p className="header__top-basket-quantity">
                    {props.productQuantity < 10
                      ? '0' + props.productQuantity
                      : props.productQuantity}
                  </p>
                </div>

                <div className="header__top-city">
                  <ul 
                    className={
                        isCitiesOpen
                        ? 'header__top-city-items header__top-city-items_opened'
                        : 'header__top-city-items'
                      }
                  >
                    <li
                      className="header__top-city-main-item"
                      onClick={handleCitiesClick}
                    >
                      <span className="arrow arrow_direct_douwn header__top-city-arrow"></span>
                      <p className="header__top-city-text text text_effect_hover-underline-white">{headerRowTopMenuItems.cities[props.currentCityKey]}</p>
                    </li>
                    {Object.keys(headerRowTopMenuItems.cities).map((item, index) => {
                      if (props.currentCityKey == item) {
                        return ''
                      } else {
                        return (
                          <li
                            key={item}
                            onClick={handleCityClick}
                            className="header__top-city-sub-item"
                          >
                            <p id={item} className="header__top-city-text text text_effect_hover-underline-white">{headerRowTopMenuItems.cities[item]}</p>
                          </li>
                        )
                      }
                    })}
                  </ul>
                  
                </div>
                <p className="header__top-phone text text_effect_hover-underline-white">8 800 555 777 3</p>

                <div className="header__top-lang">
                  <div 
                    className="header__top-lang-row header__top-lang-row_1"
                    onClick={handleLangClick}
                  >
                    <div className='header__top-lang-img' style={{ background: `url(${langImg}) center/contain no-repeat` }}
                    />
                    <p className="header__top-lang-text header__top-lang-text-row-1">{props.lang}</p>
                    <span className="arrow arrow_direct_douwn header__top-lang-arrow"></span>
                  </div>

                  <div
                    className={
                        isLangOpen
                        ? 'header__top-lang-row header__top-lang-row_2 header__top-lang-row-2_opened'
                        : 'header__top-lang-row header__top-lang-row_2'
                      }
                    id="dropdownLang"
                    onClick={handleChangeLangClick}
                  >
                    <div
                      className="header__top-lang-img"
                      style={{ background: `url(${langSubImg}) center/contain no-repeat` }}
                    />
                    <p className="header__top-lang-text header__top-lang-text-row-2">{props.lang === 'ru' ? 'en' : 'ru'}</p>
                  </div>
                </div>
              </div>

              {/* <!-- row 2 --> */}
              <nav className="header__top-nav">
                <ul className="header__top-row2">
                  {Object.keys(headerRowBottomMenuItems).map((item, index) => {                   
                    return ( 
                      <HeaderRowBootomItems
                        key={index}
                        id={item}
                        title={headerRowBottomMenuItems[item].title}
                        subItems={headerRowBottomMenuItems[item].subItems}
                      />
                    )
                  })}
                </ul>
              </nav>
            </div>
            


            {/* <!-- column 3============ --> */}
            <div className="header__top-column header__top-column_place_right" onClick={handleBunnerClick}>
              <div className="header__top-banner">
                <h2 className="header__top-banner-title">{headerBunnerMenuItems.title}</h2>
                <p className="header__top-banner-subtitile">{headerBunnerMenuItems.subtitle}</p>
                <span className="arrow arrow_direct_up header__top-banner-arrow"></span>
              </div>
              <div className={`header__top-sub-banner ${isBunnerOpen ? 'header__top-sub-banner-active' : ''}`}>
                <img src={logoImg} alt="Logo" className="logo logo_place_banner" />
                <h2 className="header__top-sub-banner-title">{headerBunnerMenuItems.title}</h2>
                <p className="header__top-sub-banner-subtitile">{headerBunnerMenuItems.subtitle}</p>
                <div className="header__top-sub-banner-img" />
                <ul className="header__top-sub-banner-list">
                  <li className="header__top-sub-banner-list-item">{headerBunnerMenuItems.listItem1}</li>
                  <li className="header__top-sub-banner-list-item">{headerBunnerMenuItems.listItem2}</li>
                  <li className="header__top-sub-banner-list-item">{headerBunnerMenuItems.listItem3}</li>
                </ul>
                <button className="header__top-sub-banner-button">{headerBunnerMenuItems.button}</button>
                <p className="header__top-sub-banner-price">{headerBunnerMenuItems.price}</p>
              </div>
            </div>
          </div>
        </div>

        {/* <!-- search================================================ --> */}
        <div className="header__search">
          <form action="#" name='headerSearchForm' className='header__search-form'>
            <button type="submit" className='header__search-submit' />
            <input type="text" name="headerSearchInput" className="header__search-input"/>
            <button type="reset" className='cross cross_size_l header__search-cross' />
          </form>
          
        </div>
      </header> 
    </>    
  );
}

export default Header;
