import React from 'react';
import HeaderRowBootomSubMenuItems from './HeaderRowBootomSubMenuItems/HeaderRowBootomSubMenuItems';


function HeaderRowBootomItems(props) {
  
  return (
    <li className="header__top-nav-item">
      <p className="header__top-nav-link text text_effect_hover-underline-white">{props.title}</p>
      <div className="header__top-submenu-wrapper">
        <div className="header__top-submenu-triangle"></div>
        <ul className="header__top-submenu">
          {Object.keys(props.subItems).map((item, index) => {                   
            return ( 
              <HeaderRowBootomSubMenuItems
                key={index}
                id={item}
                title={props.subItems[item]}
              />
            )
          })} 
        </ul>
      </div>
    </li>
  );
}

export default HeaderRowBootomItems;
