import React from 'react';

function HeaderRowBootomSubMenuItems(props) {
  return (
    <li className="header__top-submenu-item">
      <span className="header__top-submenu-text text_effect_hover-underline-white">{props.title}</span>
      <span className="triangle triangle_right"></span>
    </li>
  );
}

export default HeaderRowBootomSubMenuItems;
