
import React from 'react';
import './Intro.css';
 import './IntroMedia.css';

import { AppContext } from '../../contexts/AppContext';

// images
import imgBefore from '../../images/intro-before.png';
import imgAfter from '../../images/intro-after.png';

function Intro() {

  // contexts
  const introItems = React.useContext(AppContext).intro;

  // сравнение картинок ============================================================================
  const imgRef = React.useRef();
  const sliderRef = React.useRef();
  const wrapperCompaireRef = React.useRef();
  const imgAfterRef = React.useRef();
  const imgBeforeRef = React.useRef();
  
  let imgW;

  React.useEffect(() => {
    imgW = wrapperCompaireRef.current.offsetWidth;
    
    imgAfterRef.current.style.width = imgW + "px";
    imgBeforeRef.current.style.width = imgW + "px";

    let imgH = imgRef.current.offsetHeight;
       
    sliderRef.current.style.top = (imgH / 2) - (sliderRef.current.offsetHeight / 2) + "px";

    imgRef.current.style.width = imgW / 2 + "px";
    sliderRef.current.style.left = imgW / 2 + "px";

    sliderRef.current.addEventListener("mousedown", slideReady);
    window.addEventListener("mouseup", slideFinish);
    sliderRef.current.addEventListener("touchstart", slideReady);
    window.addEventListener("touchstop", slideFinish);

    let clicked;
    
    function slideReady(e) {
      e.preventDefault();
      clicked = 1;
      window.addEventListener("mousemove", slideMove);
    }

    function slideMove(e) {
      if (clicked == 0) return false;
      let pos = getCursorPos(e)
      if (pos < 0) pos = 0;
      if (pos > imgW) pos = imgW; 
      slide(pos);
    }

    function getCursorPos(e) {
      let a, x = 0;
      e = e || window.event; 
      a = imgRef.current.getBoundingClientRect();
      x = e.pageX - a.left - window.pageXOffset;
      return x;
    }

    function slide(x) {
      imgRef.current.style.width = x + "px";
      sliderRef.current.style.left = x + "px";
    }

    function slideFinish() {
      clicked = 0;
    }

    return () => {
      sliderRef.current.removeEventListener("mousedown", slideReady);
      window.removeEventListener("mouseup", slideFinish);
      sliderRef.current.removeEventListener("touchstart", slideReady);
      window.removeEventListener("touchstop", slideFinish);
    };
  });
  
// слайдер ===========================================================================
  const trackSliderRef = React.useRef();
  const trackRef = React.useRef();
  const [sliderItemRules, setSliderItemRules] = React.useState({});
    
  let position = 0;
  
  const slidesToScroll = 1;
  const slidesQuantinty = 4;  
  let slidesToShow;

  React.useEffect(() => {

    if (document.documentElement.clientWidth > 1023) {
      slidesToShow = 2;
    } else {
      slidesToShow = 1;
    }
    let imgSliderWidth = (trackSliderRef.current.offsetWidth - (slidesToShow - 1) * 20) / slidesToShow;
    
    setSliderItemRules({
      width: `${imgSliderWidth}px`,
    });

    trackRef.current.style.width = (imgSliderWidth + 20) * slidesQuantinty + "px";
    
    const movePosition = imgSliderWidth + 20;

    let btnNext = document.querySelector('.intro__slider-btn-right');
    let btnPrev = document.querySelector('.intro__slider-btn-left');

    btnNext.addEventListener('click', handleBtnNext);
    btnPrev.addEventListener('click', handleBtnPrev);

    function handleBtnNext() {
      if (position === -movePosition * ((slidesQuantinty - slidesToShow) / slidesToScroll)) {
        return;
      } else {
        position -= movePosition * slidesToScroll;
        trackRef.current.style.transform = `translateX(${position}px)`;
      } 
    }

    function handleBtnPrev() {
      if (position === 0) {
        return;
      } else {
        position += movePosition * slidesToScroll;
        trackRef.current.style.transform = `translateX(${position}px)`;
      } 
    } 
  }, []);


// =================================================================================

  return (
    <>
      <section className="intro container">

        <div className="intro__row">
          <div className="intro__column intro__column-compaire">

            <div className="intro__photo-main" ref={wrapperCompaireRef}>
              <div className="img__comp-img">
                <img src={imgAfter} ref={imgAfterRef} />
              </div>
              <div className="img__comp-slider" ref={sliderRef} />
              <div className="img__comp-img img__comp-overlay" ref={imgRef}>
                <img src={imgBefore} ref={imgBeforeRef} />
              </div>
            </div>
          </div>

          <div className="intro__column intro__column-slider">
            <h2 className="intro__title text__title">{introItems.title}</h2>

            <div className="intro__slider" ref={trackSliderRef}>
              <div className="intro__slider-track" ref={trackRef}>
                <div className="intro__slider-item" style={sliderItemRules}><div className="intro__slider-img" style={sliderItemRules} /></div>
                <div className="intro__slider-item" style={sliderItemRules}><div className="intro__slider-img" style={sliderItemRules} /></div>
                <div className="intro__slider-item" style={sliderItemRules}><div className="intro__slider-img" style={sliderItemRules} /></div>
                <div className="intro__slider-item" style={sliderItemRules}><div className="intro__slider-img" style={sliderItemRules} /></div>
              </div>
            </div>
            <div className="intro__slider-row">
              <button
                type='button'
                className="arrow arrow_direct_left intro__slider-btn-left"
              >
                <span></span>
              </button>
              <span className="intro__text-slider text text_effect_underline-blue ">{introItems.slider}</span>
              <button
                type='button'
                className="arrow arrow_direct_right intro__slider-btn-right"
              >
                <span></span>
              </button>
            </div>
          </div>
        </div>

      </section>
    </>
  );
}

export default Intro;
