import React from 'react';
import './App.css';
import Header from './Header/Header';
import Intro from './Intro/Intro';
import Colors from './Colors/Colors';

import '../utils/arrow.css';
import '../utils/blue-area.css';
import '../utils/container.css';
import '../utils/cross.css';
import '../utils/text.css';
import '../utils/button.css';

import { AppContext, appContext } from '../contexts/AppContext';

function App() {

  // constants==========================================================
  const [lang, setLang] = React.useState('ru');
  let currentContext = appContext[lang];
  const [currentCityKey, setCurrentCityKey] = React.useState('city1');
  const [productQuantity, setProductQuantity] = React.useState(1);

  // handlers===========================================================
  function handleCityChange(cityKey) {
    setCurrentCityKey(cityKey);
  }

  function handleLangChange() {
    if (lang === 'ru') {
      setLang('en');
    } else {
      setLang('ru');
    }
  }

  function handleWindowResize() {
    window.location.reload();
  }

  React.useEffect(() => {
    window.addEventListener('resize', handleWindowResize)
    return () => {
      window.removeEventListener('resize', handleWindowResize)
    }
  })


  return (
    <>
      <AppContext.Provider value={currentContext}>
        <Header
          lang={lang}
          onChangeLang={handleLangChange}
          currentCityKey={currentCityKey}
          onChangeCity={handleCityChange}
          productQuantity={productQuantity}
        />
        <main class="content">
          <Intro />
          <Colors />
        </main>
      </AppContext.Provider> 
    </>
  );
}

export default App;
