
import React from 'react';
import './Colors.css';
import './ColorsMedia.css';

import { AppContext } from '../../contexts/AppContext';

function Colors() {

  // contexts
  const colorsItems = React.useContext(AppContext).colors;

  const [isColorsChecked, setIsColorsChecked] = React.useState(true);
  const formRef = React.useRef();

  function handleMaterialsClick() {
    setIsColorsChecked(false);
  }

  function handleColorsClick() {
    setIsColorsChecked(true);
  }

  function handleSubmit(e) {
    e.preventDefault();
    formRef.current.reset();
  }

// =================================================================================

  return (
    <>
      <section className="colors">
        <div className="blue-area blue-area_right"></div>
        <div className="colors__containter container">
          <div className="colors__row">
            <div className="colors__column colors__column-main">
              <h2 className="colors__title text__title">{colorsItems.title}</h2>
              <p className="colors__text text__text"> {colorsItems.subtitle}</p>

              <div className="colors__insrument-row">
                <div className="colors__instrument-column colors__instrument-column_1">

                  <div className=" colors__instrument-item colors__instrument-block_instr colors__instrument-block">
                    <p className="colors__instrument-block-title"> {colorsItems.link1} </p>
                  </div>

                  <div className=" colors__instrument-item colors__instrument-block_help colors__instrument-block">
                    <p className="colors__instrument-block-title">{colorsItems.link2}</p>
                  </div>

                </div>

                <ul className="colors__instrument-column colors__instrument-column_2 colors__instrument-block">
                  <li className="colors__instrument-item colors__instrument-item_1">
                    <p className="colors__instrument-item-link">
                      <h3 className="colors__instrument-block-title">{colorsItems.link3}</h3>
                      <p className="colors__instrument-block-text">{colorsItems.link3sub}</p>
                    </p>
                  </li>
                  <li className="colors__instrument-item colors__instrument-item_2">
                    <p className="colors__instrument-item-link">
                      <h3 className="colors__instrument-block-title">{colorsItems.link4}</h3>
                      <p className="colors__instrument-block-text">{colorsItems.link4sub}</p>
                    </p>
                  </li>
                  <li className="colors__instrument-item colors__instrument-item_3">
                    <p className="colors__instrument-item-link">
                      <h3 className="colors__instrument-block-title">{colorsItems.link5}</h3>
                      <p className="colors__instrument-block-text">{colorsItems.link5sub}</p>
                    </p>
                  </li>
                </ul>
              </div>
              <button className="colors__button button">{colorsItems.button}</button>
            </div>
            
            {/* palitra ============================================================== */}
            <div className="colors__column colors__column-palitra">

              <div className="colors__instrument-column-palitra colors__instrument-column-palitra_btns">
                <button
                  onClick={handleMaterialsClick}
                  className={ isColorsChecked
                    ? "colors__instrument-palitra-item colors__instrument-materials"
                    : "colors__instrument-palitra-item colors__instrument-materials colors__instrument-active"
                  }
                  >материал отделки</button>
                <button
                  onClick={handleColorsClick}
                  className={isColorsChecked
                    ? "colors__instrument-palitra-item colors__instrument-color colors__instrument-active"
                    : "colors__instrument-palitra-item colors__instrument-color"
                  }
                >цвет</button>
              </div>

              <div className="colors__instrument-column-palitra">
                <form
                  action="#"
                  name="palitra"
                  onSubmit={handleSubmit}
                  ref={formRef}
                  className={isColorsChecked ? "colors__palitra colors__window-open" : "colors__palitra"}>
                  <p className="colors__palitra-text">{colorsItems.palitraItem1}</p>
                  <ul className="colors__palitra-main-colors">
                    <label>
                      <input name="palitra" type="radio" value="red"></input>
                      <div className="colors__palitra-item colors__palitra-main-item_1" />
                    </label>
                    <label>
                      <input name="palitra" type="radio" value="yellow"></input>
                      <div className="colors__palitra-item colors__palitra-main-item_2" />
                    </label>
                    <label>
                      <input name="palitra" type="radio" value="green"></input>
                      <div className="colors__palitra-item colors__palitra-main-item_3" />
                    </label>
                    <label>
                      <input name="palitra" type="radio" value="blue"></input>
                      <div className="colors__palitra-item colors__palitra-main-item_4" />
                    </label>
                    <label>
                      <input name="palitra" type="radio" value="grey"></input>
                      <div className="colors__palitra-item colors__palitra-main-item_5" />
                    </label>
                    <label>
                      <input name="palitra" type="radio" value="brouwn"></input>
                      <div className="colors__palitra-item colors__palitra-main-item_6" />
                    </label>
                  </ul>
                  <p className="colors__palitra-text">{colorsItems.palitraItem2}</p>
                  <div className="colors__palitra-shade-colors">
                    <ul className="colors__palitra-shade-colors-row">
                      <label>
                        <input name="palitra" type="radio" value="shade1"></input>
                        <div className="colors__palitra-item colors__palitra-shade-item_1" />
                      </label>
                      <label>
                        <input name="palitra" type="radio" value="shade2"></input>
                        <div className="colors__palitra-item colors__palitra-shade-item_2" />
                      </label>
                      <label>
                        <input name="palitra" type="radio" value="shade3"></input>
                        <div className="colors__palitra-item colors__palitra-shade-item_3" />
                      </label>
                      <label>
                        <input name="palitra" type="radio" value="shade4"></input>
                        <div className="colors__palitra-item colors__palitra-shade-item_4" />
                      </label>
                      <label>
                        <input name="palitra" type="radio" value="shade5"></input>
                        <div className="colors__palitra-item colors__palitra-shade-item_5" />
                      </label>
                      <label>
                        <input name="palitra" type="radio" value="shade6"></input>
                        <div className="colors__palitra-item colors__palitra-shade-item_6" />
                      </label>
                    </ul>
                    <ul className="colors__palitra-shade-colors-row">
                      <label>
                        <input name="palitra" type="radio" value="shade7"></input>
                        <div className="colors__palitra-item colors__palitra-shade-item_7" />
                      </label>
                      <label>
                        <input name="palitra" type="radio" value="shade8"></input>
                        <div className="colors__palitra-item colors__palitra-shade-item_8" />
                      </label>
                      <label>
                        <input name="palitra" type="radio" value="shade9"></input>
                        <div className="colors__palitra-item colors__palitra-shade-item_9" />
                      </label>
                      <label>
                        <input name="palitra" type="radio" value="shade10"></input>
                        <div className="colors__palitra-item colors__palitra-shade-item_10" />
                      </label>
                      <label>
                        <input name="palitra" type="radio" value="shade11"></input>
                        <div className="colors__palitra-item colors__palitra-shade-item_11" />
                      </label>
                      <label>
                        <input name="palitra" type="radio" value="shade12"></input>
                        <div className="colors__palitra-item colors__palitra-shade-item_12" />
                      </label>
                    </ul>
                  </div>
                  <button type='submit' className="colors__palitra-button">{colorsItems.palitraButton}</button>
                </form>

                <div className={ isColorsChecked ? "colors__materials" : "colors__materials colors__window-open"}></div>              
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
}

export default Colors;
