import React from 'react';

function HeaderSubMenuItem(props) {
  return (
    <li className="header__left-submenu-item">
      <span
        id={props.id}
        className="text 
                   text_effect_hover-underline-white
                   header__left-submenu-text"
      >
        {props.title}
      </span>
    </li>
  );
}

export default HeaderSubMenuItem;
